import React from 'react'

export default function Header(props) {
    function navToggle() {
        const tabletNav = document.getElementById('tablet-nav-overlay')
        tabletNav.classList.toggle('active')
        tabletNav.classList.toggle('overlay-active')
        document.getElementById('span1').classList.toggle('nav-toggle-move-up')
        document.getElementById('span2').classList.toggle('nav-toggle-rorate-ac')
        document.getElementById('span3').classList.toggle('nav-toggle-rotate-c')
        document.getElementById('span4').classList.toggle('nav-toggle-move-down')
    }

    function removeTabNav(e) {
        const tabletNav = document.getElementById('tablet-nav-overlay')
        tabletNav.classList.remove('active')
        tabletNav.classList.remove('overlay-active')
        document.getElementById('span1').classList.remove('nav-toggle-move-up')
        document.getElementById('span2').classList.remove('nav-toggle-rorate-ac')
        document.getElementById('span3').classList.remove('nav-toggle-rotate-c')
        document.getElementById('span4').classList.remove('nav-toggle-move-down')
        const animateOffLoad = document.querySelector(".float-col-30")
        animateOffLoad.style.width = '100%'
        props.changePage(e)
    }

    return (
        <>
            <div className='flex-container' id='navScroll'>
                <div className='basic-details-container'>
                    <div className='float-left bullet-container'>
                        <div className='bullet'></div>
                    </div>
                    <div className='float-left details-container'>
                        <div className='details-container-inner'><span className='name'><b>Sai Lokesh Gorantla</b></span> <span className='headerOr'>|</span> <span className='designation'>UG Student, EE Dept.</span></div>
                    </div>
                </div>

                <div className='nav-container' id='nav-container-id'>
                    <a className='nav-toggle-btn' onClick={() => navToggle()}>
                        <span id='span1'></span>
                        <span id='span2'></span>
                        <span id='span3'></span>
                        <span id='span4'></span>
                    </a>

                    <ul className='desktop-nav'>
                        <li><a id='contactActive' onClick={() => props.changePage(4)}>Contact Me</a></li>
                        <li><a href='https://drive.google.com/file/d/1s98MRBEmmS9pIZVymiedzHVa_anRi-q7/view?usp=sharing' target='_blank' onClick={() => props.changePage(3)}>Resume</a></li>
                        <li><a id='projectsActive' onClick={() => props.changePage(2)}>Projects</a></li>
                        <li><a id='eduActive' onClick={() => props.changePage(1)}>Experience &amp; Skills</a></li>
                        <li><a className='navActive' id='aboutActive' onClick={() => props.changePage(0)}>About Me</a></li>
                    </ul>

                </div>
            </div>
            <div id='tablet-nav-overlay'>
                <ul className='tablet-nav'>
                    <li><a id='aboutActiveTab' className='navActive' onClick={() => removeTabNav(0)}>About Me</a></li>
                    <li><a id='eduActiveTab' onClick={() => removeTabNav(1)}>Experience &amp; Skills</a></li>
                    <li><a id='projectsActiveTab' onClick={() => removeTabNav(2)}>Projects</a></li>
                    <li><a href='https://drive.google.com/file/d/1s98MRBEmmS9pIZVymiedzHVa_anRi-q7/view?usp=sharing' onClick={() => props.changePage(3)} className='tabNavClick' target='_blank'>Resume</a></li>
                    <li><a id='contactActiveTab' onClick={() => removeTabNav(4)}>Contact Me</a></li>
                </ul>
            </div>
        </>
    )
}
