import React from 'react'

export default function Project(props) {
    return (
        <>
            <div className='projectContainer transition-fade' id='aboutSection'>
                <div className='float-col-30'></div>
                <div className='float-col-70'>
                    <div className='float30-transition'></div>
                </div>
                <div className='project-details-cover'>
                    <div className=''>
                        <div className='project-container'>
                            <br></br>
                            <div className='project-details project-details-enter-animation'>
                                <div className='blue-bar'></div>
                                <div className='projectName'>
                                    <span className='projName'>{props.project.name}</span><br></br>
                                    <span className='shortDesc'>{props.project.shortDesc}</span>
                                </div>
                                <div className='projectDesc'>
                                    <p>
                                        {props.project.desc}
                                    </p>
                                    <a href={props.project.link} target='_blank' className='project-files-button' onClick={() => {props.linkClick(props.project.key)}}>Link</a>
                                </div>
                            </div>
                            <div className='project-picture project-picture-enter-animation'>
                                <img src={props.project.imgSrc} className='lazy' loading='lazy'></img>
                            </div>
                        </div>
                        <br></br>
                    </div>
                </div>
            </div>

        </>
    )
}
