import React, { useState, useEffect } from 'react'

export default function Contact(props) {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [contact, setContact] = useState('');
    const [query, setQuery] = useState('');

    const handleNameChange = (e) => {
        setName(e.target.value);
    }
    const handleEmailChange = (e) => {
        setEmail(e.target.value);
    }
    const handleContactChange = (e) => {
        setContact(e.target.value);
    }
    const handleQueryChange = (e) => {
        setQuery(e.target.value);
    }

    var resubmitValidity = 0

    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    headers.append('Accept', 'application/json');
    headers.append('Origin', 'https://portfolioapi-lokesh.herokuapp.com');
    function sendMessage(e) {
        document.getElementById('submitForm').innerHTML = '<div class="spinner-border text-primary" role="status"><span class="sr-only">Loading...</span></div>'
        fetch('https://geolocation-db.com/json/')
            .then((response) => response.json())
            .then((data) => post(data))
            .catch(() => {
                const timestamp = new Date();
                const month = parseInt(timestamp.getMonth()) + 1
                const state = {
                    "name": name,
                    "email": email,
                    "contact": contact,
                    "query": query,
                    "timestamp": timestamp.getDate() + "/" + month + "/" + timestamp.getFullYear() + ", " + timestamp.toLocaleTimeString(),
                    "validity": resubmitValidity
                }
                fetch('https://portfolioapi-lokesh.herokuapp.com/contact', {
                    method: 'POST',
                    body: JSON.stringify(state),
                    headers: headers
                })
                    .then((response) => response.json())
                    .then((data) => {
                        if(data['success'] === 'True'){
                            document.getElementById('submitForm').innerHTML = 'SENT'
                            document.getElementById('submitForm').style.color = '#39C16C'
                        }
                        else{
                            resubmitValidity = 1
                            document.getElementById('submitForm').innerHTML = 'Try Again'
                            document.getElementById('submitForm').style.color = '#FF7900'
                            window.alert('Our algorithm has detected abnormality in the details you provided. Please verify and resubmit if you are sure they are correct.')
                        }
                    })
                    .catch((e) => {
                        console.log(e)
                        document.getElementById('submitForm').innerHTML = 'Try Again'
                        document.getElementById('submitForm').style.color = '#FF7900'
                    });
            });

        function post(e) {
            const timestamp = new Date();
            const state = {
                "name": name,
                "email": email,
                "contact": contact,
                "query": query,
                "timestamp": timestamp.getDate() + "/" + timestamp.getMonth() + "/" + timestamp.getFullYear() + ", " + timestamp.toLocaleTimeString(),
                "IPv4": e.IPv4,
                "city": e.city,
                "country_code": e.country_code,
                "country_name": e.country_name,
                "latitude": e.latitude,
                "longitude": e.longitude,
                "postal": e.postal,
                "state": e.state,
                "validity": resubmitValidity
            }
            fetch('https://portfolioapi-lokesh.herokuapp.com/contact', {
                method: 'POST',
                body: JSON.stringify(state),
                headers: headers
            })
                .then((response) => response.json())
                .then((data) => {
                    if(data['success'] === 'True'){
                        document.getElementById('submitForm').innerHTML = 'SENT'
                        document.getElementById('submitForm').style.color = '#39C16C'
                    }
                    else{
                        resubmitValidity = 1
                        document.getElementById('submitForm').innerHTML = 'Try Again'
                        document.getElementById('submitForm').style.color = '#FF7900'
                        window.alert('Our algorithm has found out that the provided details are absurd. Please verify and resubmit if you are sure they are correct.')
                    }
                })
                .catch((e) => {
                    console.log(e)
                    document.getElementById('submitForm').innerHTML = 'Try Again'
                    document.getElementById('submitForm').style.color = '#FF7900'
                });
        }
        e.preventDefault();
    };
    return (
        <>
            <div className='headingContainer headingMargin' id='educationSection'>
                <div className='float-col-60' style={{ background: '#fff' }}>
                    <div className='float40-transition' id='bg-animation10'></div>
                </div>
                <div className='float-col-40' style={{ background: '#e6dace' }}></div>
                <div className='heading-cover heading-cover-enter-animation-dual' id="heading-cover-animation4">
                    <h1 style={{ textAlign: 'left' }}>Get in Touch</h1>
                </div>
            </div>
            <div className='headingContainer transition-fade' id='educationSection'>
                <div className='float-col-60' style={{ background: '#fff' }}>
                    <div className='float40-transition' id='bg-animation11'></div>
                </div>
                <div className='float-col-40' style={{ background: '#e6dace' }}></div>
                <div className='heading-cover heading-cover-enter-animation-dual' id="heading-cover-animation5">
                    <p>
                        Got a project idea? Feel free to elaborate the project
                        <br />
                        Let's work in synergy and make amazing products
                    </p>
                </div>
            </div>
            <div className='contactContainer'>
                <div className='float-col-60' style={{ background: '#fff' }}>
                    <div className='float40-transition' id='bg-animation12'></div>
                </div>
                <div className='float-col-40' style={{ background: '#e6dace' }}></div>
                <div className='contact-cover contact-cover-enter-animation' id='contact-cover-animation'>
                    <div className="contact-form">
                        <div className="first-container">
                            <div className="info-container">
                                <div>
                                    <h3>Address</h3>
                                    <p>Hyderabad, Telangana</p>
                                </div>
                                <div>
                                    <h3>Lets Talk</h3>
                                    <a href="tel:+917330755757" className="linkContact">+91 73307 55757</a>
                                </div>
                                <div>
                                    <h3>Mail</h3>
                                    <a href="mailto:g.sailokesh9@gmail.com" className="linkContact">g.sailokesh9@gmail.com</a>
                                </div>
                            </div>
                        </div>
                        <div className="second-container">
                            <h2>Send A Message</h2>
                            <form onSubmit={(e) => sendMessage(e)} className='contactForm'>
                                <div className="form-group">
                                    <input id="name-input" type="text" placeholder="Name"  value={name} onChange={(e) => { handleNameChange(e) }} />
                                </div>
                                <div className="form-group">
                                    <input id="email-input" type="email" placeholder="Email"  value={email} onChange={(e) => { handleEmailChange(e) }} />
                                </div>
                                <div className="form-group">
                                    <input id="phone-input" type='number' placeholder="Contact number"  value={contact} onChange={(e) => { handleContactChange(e) }} />
                                </div>
                                <div className="form-group">
                                    <textarea id="message-textarea" placeholder="Write a message" value={query} onChange={(e) => { handleQueryChange(e) }} ></textarea>
                                </div>
                                <button type='submit' id='submitForm' style={{'outline': 'none'}}>Send message</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
