import React from 'react'

export default function About(props) {
    return (
        <>
            <div className='aboutContainer' id='aboutSection'>
                <div className='float-col-30'></div>
                <div className='float-col-70'>
                    <div className='float30-transition' id='bg-animation1'></div>
                </div>
                <div className='about-details-cover'>
                    <div className='about-picture'>
                        <div className='about-pic-container about-pic-container-enter-animation' id='about-pic-animation'>
                            <br></br>
                            <div className='pic-container' id='profile' >
                                <img src={require("./Lokesh.jpg")} className='picture'></img>
                            </div>
                            <br></br>
                            <div className='picture-name'>Sai Lokesh Gorantla</div>
                            <br></br>
                            <hr className='picture-rule'></hr>
                            <br></br>
                            <div className='picture-designation'>UG Student, EE Dept.<br></br>At IIT Kharagpur</div>
                            <br></br>
                        </div>
                        <div className='picture-connect picture-connect-enter-animation' id='picture-connect-animation'>
                            <div className='connect-container-inner'>
                                <ul>
                                    <li id='facebookLink'><a href='https://www.facebook.com/sailokesh.gorantla' target='_blank' className='connectLinks' onClick={() => {props.socialLink(0)}}><i className="fa-brands fa-facebook"></i></a></li>
                                    <li id='linkedinLink'><a href='https://www.linkedin.com/in/sai-lokesh-gorantla-a2b110206/' target='_blank' className='connectLinks' onClick={() => {props.socialLink(1)}}><i className="fa-brands fa-linkedin"></i></a></li>
                                    <li><a href='https://github.com/it-is-lokesh' target='_blank' className='connectLinks' onClick={() => {props.socialLink(2)}}><i className="fa-brands fa-github"></i></a></li>
                                    <li id='instagramLink'><a href='https://www.instagram.com/__it_is_lokesh__/' target='_blank' className='connectLinks' onClick={() => {props.socialLink(3)}}><i className="fa-brands fa-instagram"></i></a></li>
                                </ul>
                            </div>
                        </div>

                    </div>
                    <div className='about-desc about-desc-enter-animation' id='about-desc-animation'>
                        <br></br>
                        <div className='desc-hello typewriter'>Hey there!</div>
                        <div className='desc-intro'>Here's who I am &amp; what I do</div>
                        <br></br>
                        <div className='desc-btns' style={{'marginBottom': '20px'}}>
                            <a onClick={() => props.changePage(2)} className='link-no-dec about-desc-btns'>Projects</a>
                            <a onClick={() => props.changePage(4)} className='link-no-dec about-desc-btns'>Talk to me</a>
                        </div>
                        <p className='desc-detail'>
                            I am a 3<sup>rd</sup> year Undergraduate student persuing B.Tech from IIT Kharagpur.
                            Enthuiastic about Robotics and Machine Learning, I am currently learning various Control mechanisms 
                            for a drone project!
                            <br />
                            <br />
                            Other areas of Interest:<br />
                            &#8212; Control Systems<br></br>
                            &#8212; Reinforcement Learning<br></br>
                            {/* &#8212; Control Systems Engineering<br></br> */}
                        </p>
                    </div>
                </div>
            </div>
            <div className='longDescContainer transition-fade'>
                <div className='float-col-30'></div>
                <div className='float-col-70'>
                    <div className='float30-transition' id='bg-animation2'></div>
                </div>
                <div className='long-details-cover long-details-cover-enter-animation' id='long-details-animation'>
                    I have a keen interest to explore and understand the internal
                    configuration of electronic devices that I have used since my childhood, and the
                    field of Embedded Controls and Communication best fits my ideas.
                    Coincidentally, I have landed in the Instrumentation Branch which encouraged
                    me to persue my hobby as a career. In my first year, I have attended a workshop
                    on autonomous robotics organised by the Technology Robotix Society in association
                    with IEEE, which got me acquainted with this amazing field of Embedded Electronics.
                    <br /><br />
                    In the third semester of study at IIT Kharagpur, I have taken a course on Embedded
                    Controls and Software, in whihc I learnt about the AVR programming and have also
                    built small prototypes to brush up my skills. Recently I have been exploring the
                    field of Machine Learning and Deep Learning and realised its potential in Computer
                    Vision and modern day robots.
                    <br /><br />
                    Apart from academics, I love to travel and meet new people everyday and am a great
                    admirer of science fiction.
                </div>
            </div>

            <div className='headingContainer transition-fade'>
                <div className='float-col-30'></div>
                <div className='float-col-70'>
                    <div className='float30-transition' id='bg-animation3'></div>
                </div>
                <div className='heading-cover heading-cover-enter-animation' id="heading-cover-animation1">
                    <h1>Education</h1>
                </div>
            </div>

            <div className='educationContainer transition-fade' id='educationSection'>
                <div className='float-col-30'></div>
                <div className='float-col-70'>
                    <div className='float30-transition' id='bg-animation4'></div>
                </div>
                <div className="steps-progress education-details-cover education-details-cover-enter-animation" id='education-details-animation'>
                    <ul className="steps-list">
                        <li className="steps-item">
                            <h3 className="head">Indian Institute of Technology Kharagpur</h3>
                            {/* <div className="location">Indonesia - University</div> */}
                            <div className="periode"><i className="fas fa-calendar-alt"></i> 2020 - 2024</div>
                        </li>
                        <li className="steps-item">
                            <h3 className="head">Sri Chaitanya Junior College</h3>
                            {/* <div className="location">Indonesia - Company</div> */}
                            <div className="periode"><i className="fas fa-calendar-alt"></i> 2018 - 2020</div>
                        </li>
                        <li className="steps-item">
                            <h3 className="head">DAV Public School, Kukatpally</h3>
                            {/* <div className="location">Indonesia - Company</div> */}
                            <div className="periode"><i className="fas fa-calendar-alt"></i> 2018</div>
                        </li>
                    </ul>
                </div>
            </div>
        </>
    )
}
